.flex-container {
  @media screen and (max-width: 767px) {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
  }
}

.program {
  @media screen and (max-width: 767px) {
    padding-bottom: 17.5px;
  }

  @media screen and (min-width: $breakpoint-tablet) {
    padding-top: 28px;
    padding-bottom: 40px;
  }

  @media screen and (min-width: $breakpoint-decktop) {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  &__wrapper {
    @media screen and (min-width: $breakpoint-tablet) {
      min-width: 329px;
      margin-right: 39px;
    }

    @media screen and (min-width: $breakpoint-decktop) {
      max-width: 417px;
      margin-right: 116px;
      padding-top: 61px;
    }
  }

  &__title {
    font-family: 'Montserrat Alternates';
    color: $main-color;

    @media screen and (max-width: 767px) {
      text-align: center;
      margin-left: auto;
      margin-right: auto;
      font-weight: $font-w-500;
      font-size: 24px;
      line-height: 1.2;
      margin-bottom: 8px;
    }

    @media screen and (min-width: $breakpoint-tablet) {
      text-align: left;
      font-weight: $font-w-500;
      font-size: 32px;
      line-height: 1.21;
      margin-bottom: 28px;
    }

    @media screen and (min-width: $breakpoint-decktop) {
      text-align: left;
      font-weight: $font-w-500;
      font-size: 32px;
      line-height: 1.21;
      margin-bottom: 24px;
    }
  }

  &__text {
    font-family: 'Montserrat';
    color: $main-color;
    font-weight: 400;
    font-size: $font-sz-16;
    line-height: 1.25;

    @media screen and (max-width: 767px) {
      margin-bottom: 8px;
    }

    @media screen and (min-width: $breakpoint-tablet) {
      margin-bottom: 12px;
    }

    @media screen and (min-width: $breakpoint-decktop) {
      width: 392px;
      font-size: $font-sz-18;
      line-height: 1.22;

      margin-bottom: 20px;
    }
  }

  &__list {
    @media screen and (max-width: 767px) {
      margin-bottom: 16px;
    }

    @media screen and (min-width: $breakpoint-tablet) {
      margin-bottom: 28px;
    }

    @media screen and (min-width: $breakpoint-decktop) {
      margin-bottom: 20px;
    }
  }

  &__item {
    @media screen and (max-width: 767px) {
      display: flex;
      align-items: center;

      // max-width: 279px;
      margin: 0 auto;

      &:not(:last-child) {
        margin-bottom: 8px;
      }
    }
    @media screen and (min-width: $breakpoint-tablet) {
      display: flex;
      align-items: center;

      &:not(:last-child) {
        margin-bottom: 12px;
      }
    }

    @media screen and (min-width: $breakpoint-decktop) {
      display: flex;
      align-items: center;

      &:not(:last-child) {
        margin-bottom: 17px;
      }
    }
  }

  &__icon {
    @media screen and (max-width: 767px) {
      margin-right: 8px;
    }

    @media screen and (min-width: $breakpoint-tablet) {
      margin-right: 8px;
    }

    @media screen and (min-width: $breakpoint-decktop) {
      margin-right: 16px;
    }
  }

  &__list-text {
    font-weight: 400;
    font-size: $font-sz-16;
    line-height: 1.25;

    @media screen and (min-width: $breakpoint-decktop) {
      font-size: $font-sz-18;
      line-height: 1.22;
    }
  }

  &__link {
    font-family: 'Montserrat';
    color: $main-color;
    font-weight: $font-w-500;

    @media screen and (max-width: 767px) {
      font-size: $font-sz-16;
      line-height: 1.25;

      display: inline-block;
      padding-bottom: 24px;
    }

    @media screen and (min-width: $breakpoint-tablet) {
      font-size: $font-sz-18;
      line-height: 1.22;
    }

    @media screen and (min-width: $breakpoint-decktop) {
      display: none;
    }

    &:hover,
    &:focus {
      color: $accent-pink-color;
    }
  }

  &__examples-container {
    display: flex;
    flex-wrap: wrap;
    width: 280px;
    margin: 0 auto;

    @media screen and (min-width: $breakpoint-tablet) {
      margin-top: 19px;
      min-width: 350px;
    }

    @media screen and (min-width: $breakpoint-decktop) {
      min-width: 714px;
    }
  }

  &__examples-bg {
    &:nth-child(2) {
      padding-top: 28px;
      margin-left: 20px;
    }

    &:nth-child(4) {
      padding-top: 28px;
      margin-left: 20px;
    }

    @media screen and (min-width: $breakpoint-tablet) {
      // &:nth-child(2) .program__examples-title {
      //   // letter-spacing: -0.2;
      // }

      &:nth-child(2) {
        padding-top: 0;
        margin-left: 32px;
      }

      &:nth-child(3) {
        margin-top: -9px;
      }
      &:nth-child(4) {
        margin-left: 32px;
      }
    }

    @media screen and (min-width: $breakpoint-decktop) {
      &:nth-child(2) {
        padding-top: 36px;
        margin-left: 128px;
      }

      &:nth-child(4) {
        padding-top: 36px;
        margin-left: 128px;
      }
    }
  }

  &__img {
    border: 1px solid $accent-pink-color;
    border-radius: 50%;
    width: 130px;
    height: 130px;
    position: relative;
    filter: drop-shadow(10px 15px 25px rgba(210, 201, 201, 0.45));

    @media screen and (min-width: $breakpoint-tablet) {
      width: 152px;
      height: 152px;
    }

    @media screen and (min-width: $breakpoint-decktop) {
      width: 288px;
      height: 288px;
    }

    &::before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      // z-index: 15;
      background: linear-gradient(
        0deg,
        #000000 -12%,
        rgba(156, 156, 156, 0) 56.6%
      );
    }
  }

  &__examples-wrapper {
    position: absolute;
    width: 100%;
    top: 45%;
    text-align: center;

    @media screen and (min-width: $breakpoint-tablet) {
      top: 53%;
    }

    @media screen and (min-width: $breakpoint-decktop) {
      top: 67%;
    }
  }

  &__examples-bg--position {
    position: relative;
  }

  &__examples-title {
    font-family: 'Montserrat Alternates';
    color: $white-color;

    @media screen and (max-width: 767px) {
      font-weight: $font-w-500;
      font-size: 14px;
      line-height: 1.21;

      margin-bottom: 8px;
    }

    @media screen and (min-width: $breakpoint-tablet) {
      font-weight: $font-w-500;
      font-size: $font-sz-16;
      line-height: 1.25;

      margin-bottom: 8px;
    }

    @media screen and (min-width: $breakpoint-decktop) {
      font-weight: $font-w-500;
      font-size: 24px;
      line-height: 1.2;

      margin-bottom: 16px;
    }
  }

  &__examples-text {
    font-family: 'Montserrat';
    color: $white-color;

    @media screen and (max-width: 767px) {
      font-weight: 400;
      font-size: 12px;
      line-height: 1.25;
    }

    @media screen and (min-width: $breakpoint-tablet) {
      font-weight: 400;
      font-size: 14px;
      line-height: 1.21;
    }

    @media screen and (min-width: $breakpoint-decktop) {
      font-weight: 400;
      font-size: $font-sz-16;
      line-height: 1.25;
    }
  }
  &__text--desktop {
    display: none;

    @media screen and (min-width: $breakpoint-decktop) {
      display: block;
      width: 392px;
      font-weight: 400;
      font-size: $font-sz-18;
      line-height: 1.22;
    }
  }
}
